.container{
    color:#fff;
}

.ul{
    padding:0px;
}

li{
    list-style:none;
    display: inline;
    padding-bottom: 5px;
    margin-right: 20px;
}
li::after{
    border-right: 1px solid #fff;
}
.title{
    position: relative;
}

.detail__top{
    text-align: center;
}
.detail__top img:first-child{
    z-index: 2;
    top:100px;
    position: relative;
    box-shadow: -5px 5px 25px 3px rgb(51 51 51)

}
.detail__top img:last-child{
    position: absolute;
    opacity: 50%;
    left: 18%;
    top: 20%;
    display: inline;
}

.detail__bottom {
    margin-top: 200px;
    left:15%;
    position:relative;
}

.overview{
    width: 65%;
}